import React, { Component } from "react";
import { Link } from "@reach/router";
import Icon from "icon/createIcon";
import Header from "header/header";
import styles from "./select.module.css";

export default class SelectLogin extends Component {
  render() {
    return (
      <div style={{ height: "100%" }}>
        <Header />
        <div className={styles.pictureWrapper}>
          <img
            alt=""
            className={styles.picture}
            src="//yanxuan.nosdn.127.net/bd139d2c42205f749cd4ab78fa3d6c60.png"
          />
        </div>
        <div className={styles.toBeCenter}>
          <Link to="/test/user/login">
            <div className={styles.loginButton}>
              <Icon style={styles.email} name={"email"} />
              邮箱帐号登录
            </div>
          </Link>
          <Link to="register">
            <div className={styles.emailRegister}>
              邮箱帐号注册
              <Icon name={"return"} />
            </div>
          </Link>
        </div>
      </div>
    );
  }
}



