import React, { Component } from "react";
import { InputItem, Toast, List, Modal } from "antd-mobile";
import { createForm } from "rc-form";
import { JSEncrypt } from "jsencrypt";
import { navigate } from "@reach/router";
import Header from "header/header";
import styles from "./register.module.css";
const dialogue = Modal.alert;

class emailState extends Component {
  constructor() {
    super(...arguments);
    this.state = {
      emailReiteration: false,
      emailErrorMessage: "",
      emailError: false,
      passwordError: false,
      passwordErrorMessage: "",
      publicKey: ""
    };
    this.encryptor = new JSEncrypt();
    this.emailVerify = this.emailVerify.bind(this);
    this.passwordVerify = this.passwordVerify.bind(this);
    this.emailError = this.emailError.bind(this);
    this.passwordError = this.passwordError.bind(this);
    this.examineError = this.examineError.bind(this);
    const getAjax = new XMLHttpRequest();
    getAjax.open("GET", "/public_key");
    getAjax.onload = () => {
      if (getAjax.readyState === 4 && getAjax.status === 200) {
        this.setState({
          publicKey: JSON.parse(getAjax.response).key
        });
      }
    };
    getAjax.send();
  }
  examineError() {
    const {
      emailError,
      passwordError,
      emailErrorMessage,
      passwordErrorMessage
    } = this.state;
    this.encryptor.setPublicKey(this.state.publicKey);
    if (emailError || passwordError) {
      if (emailError) {
        return dialogue("", emailErrorMessage, [{ text: "确定" }]);
      } else {
        return dialogue("", passwordErrorMessage, [{ text: "确定" }]);
      }
    }
    const { emailState, passwordState } = this.props.form.getFieldsValue([
      "emailState",
      "passwordState"
    ]);
    if (!emailState || !passwordState) {
      return dialogue("", "请输入数据", [{ text: "确定" }]);
    }
    const userMessage = {
      emailAddress: emailState,
      password: this.encryptor.encrypt(passwordState)
    };
    const getAjax = new XMLHttpRequest();
    getAjax.open("POST", "/register");
    getAjax.setRequestHeader("content-type", "application/json");
    getAjax.onload = () => {
      if (getAjax.readyState === 4 && getAjax.status === 200) {
        const { registrationCreate, message } = JSON.parse(getAjax.response);
        if (!registrationCreate) {
          Toast.info(message, 1);
        } else {
          navigate("/user", {
            state: {
              login: true
            },
            replace: true
          });
        }
      }
    };
    getAjax.send(JSON.stringify(userMessage));
  }
  emailVerify() {
    const errorMessage = this.props.form.getFieldError("emailState");
    if (errorMessage) {
      return this.setState({
        emailError: true,
        emailErrorMessage: errorMessage
      });
    } else {
      const address = this.props.form.getFieldValue("emailState");
      const getAjax = new XMLHttpRequest();
      getAjax.open("GET", `/user_duplicate_checking?email=${address}`);
      getAjax.onload = () => {
        if (getAjax.readyState === 4 && getAjax.status === 200) {
          const userRepetition = JSON.parse(getAjax.response).status;
          if (userRepetition === "已创建") {
            return this.setState({
              emailError: true,
              emailErrorMessage: "该帐号已存在"
            });
          }
        }
      };
      getAjax.send();
    }
    this.setState({
      emailError: false,
      emailErrorMessage: ""
    });
  }
  passwordVerify() {
    if (!this.props.form.getFieldError("passwordState")) {
      this.setState({
        passwordError: false,
        passwordErrorMessage: ""
      });
    } else {
      const message = this.props.form.getFieldError("passwordState")[0];
      this.setState({
        passwordError: true,
        passwordErrorMessage: message
      });
    }
  }
  emailError = () => {
    if (!this.state.emailError) {
      return false;
    }
    Toast.info(this.state.emailErrorMessage, 1);
  };
  passwordError = () => {
    if (!this.state.passwordError) {
      return;
    }
    Toast.info(this.state.passwordErrorMessage, 1);
  };
  render() {
    const { getFieldProps } = this.props.form;
    return (
      <div style={{ height: window.innerHeight }} className={styles.wrapper}>
        <Header />
        <List>
          <InputItem
            {...getFieldProps("emailState", {
              rules: [
                {
                  type: "email",
                  message: "帐号格式错误"
                },
                {
                  required: true,
                  message: "请输入帐号"
                }
              ]
            })}
            type={"text"}
            clear
            extra={this.state.emailReiteration ? "帐号已存在" : null}
            error={this.state.emailError}
            onErrorClick={this.emailError}
            placeholder="邮箱帐号"
            onBlur={this.emailVerify}
          />
          <InputItem
            {...getFieldProps("passwordState", {
              rules: [
                {
                  required: true,
                  message: "请输入密码"
                }
              ]
            })}
            type={"password"}
            clear
            error={this.state.passwordError}
            onErrorClick={this.passwordError}
            placeholder="密码"
            onBlur={this.passwordVerify}
          />
        </List>
        <div className={styles.registerButton} onClick={this.examineError}>
          注册
        </div>
      </div>
    );
  }
}
export default createForm()(emailState);



