import React, { Component, Fragment } from "react";
import styles from "./index.module.css";
import Icon from "icon/createIcon";
import { Modal } from "antd-mobile";
import { navigate } from "@reach/router";

const listState = [
  {
    logoName: "together",
    name: "周六一起拼"
  },
  {
    logoName: "address",
    name: "地址管理"
  },
  {
    logoName: "safety",
    name: "帐号安全"
  }
];
class UserDetails extends Component {
  constructor() {
    super(...arguments);
    this.logOut = this.logOut.bind(this);
    this.closeAlert = this.closeAlert.bind(this);
    this.state = {
      alert: false
    };
    this.quit = this.quit.bind(this);
  }
  closeAlert() {
    this.setState({
      alert: false
    });
  }
  logOut() {
    this.setState({
      alert: true
    });
  }
  quit() {
    const getAjax = new XMLHttpRequest();
    getAjax.open("DELETE", "/posts/login");
    getAjax.setRequestHeader("content-type", "application/json");
    getAjax.onload = () => {
      if (getAjax.readyState === 4 && getAjax.status === 200) {
        navigate("/user", {
          state: {
            login: false
          }
        });
      }
    };
    getAjax.send();
  }
  render() {
    return (
      <div
        style={{
          height: window.innerHeight
        }}
        className={styles.wrapper}
      >
        <div className={styles.header}>
          <img
            src="//yanxuan.nosdn.127.net/8945ae63d940cc42406c3f67019c5cb6.png"
            className={styles.portrait}
            alt={""}
          />
          <div className={styles.info}>
            <div className={styles.name}>w729993031</div>
          </div>
        </div>
        <div className={styles.list}>
          {listState.map(({ logoName, name }) => (
            <Fragment key={name}>
              <div className={styles.itemWrapper}>
                <div className={styles.item}>
                  <Icon name={logoName} style={styles.itemLogo} />
                  {name}
                </div>
              </div>
            </Fragment>
          ))}
        </div>
        <div className={styles.userQuit} onClick={this.logOut}>
          退出登录
        </div>
        <Modal
          visible={this.state.alert}
          popup={true}
          transparent={true}
          wrapClassName={styles.alert}
          footer={[
            {
              text: "取消",
              onPress: () => {
                this.closeAlert();
              }
            },
            {
              text: "确定",
              onPress: () => {
                this.quit();
              }
            }
          ]}
        >
          退出登录?
        </Modal>
      </div>
    );
  }
}

export default UserDetails;



